"use client";
import { useEffect, useState } from "react";
import Button from "@/components/ui/button/Button";
import Input from "@/components/form/input/InputField";
import Label from "@/components/form/Label";
import { CompanyService } from "@/services/company.service";


export interface BranchFormData {
branchName: string;
  companyId: string;
  address: string;
  phone: string;
}

interface Company {
  id: string;
  name: string;
}


interface Props {
  open: boolean;
  mode: "add" | "edit";
  initialData?: BranchFormData;
  onClose: () => void;
  onSubmit: (data: BranchFormData) => void;
}

const emptyForm: BranchFormData = {
  companyId: "",
  branchName: "",
  address: "",
  phone: "",
};

export default function BranchFormModal({
  open,
  mode,
  initialData,
  onClose,
  onSubmit,
}: Props) {
  const [form, setForm] = useState<BranchFormData>(emptyForm);
  const [errors, setErrors] =
    useState<Partial<Record<keyof BranchFormData, string>>>({});
  const [loading, setLoading] = useState(false);

  const [companies, setCompanies] = useState<Company[]>([]);
const [loadingCompanies, setLoadingCompanies] = useState(false);


useEffect(() => {
  if (!open) return;

  const fetchCompanies = async () => {
    try {
      setLoadingCompanies(true);

      const res = await CompanyService.getAll();

      // ‚úÖ CORRECT: use the array
      setCompanies(res.data.data);

    } catch (err) {
      console.error("Failed to load companies", err);
      setCompanies([]); // safety fallback
    } finally {
      setLoadingCompanies(false);
    }
  };

  fetchCompanies();
}, [open]);


useEffect(() => {
  setForm(
    mode === "edit" && initialData
      ? initialData
      : emptyForm
  );
  setErrors({});
}, [mode, initialData, open]);


  useEffect(() => {
    const esc = (e: KeyboardEvent) => e.key === "Escape" && onClose();
    document.addEventListener("keydown", esc);
    return () => document.removeEventListener("keydown", esc);
  }, [onClose]);



  const update = (k: keyof BranchFormData, v: string) =>
    setForm((p) => ({ ...p, [k]: v }));

  const validate = () => {
    const e: typeof errors = {};
    if (!form.branchName.trim()) e.branchName = "Branch Name is required";
    if (!form.companyId.trim()) e.companyId = "Company Name is required";

    if (!form.address.trim()) e.address = "Address is required";
    if (!form.phone.trim()) e.phone = "Phone is required";
    setErrors(e);
    return !Object.keys(e).length;
  };

  const submit = async () => {
    if (!validate() || loading) return;
    setLoading(true);
    await onSubmit(form);
    setLoading(false);
  };

  if (!open) return null;

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50">
      <div
        className="w-full max-w-3xl bg-white dark:bg-gray-900 rounded-xl shadow-xl"
        onClick={(e) => e.stopPropagation()}
      >
        {/* Header (Centered) */}
        <div className="relative p-5 border-b dark:border-gray-800">
          <h3 className="text-lg font-semibold text-center text-gray-900 dark:text-white">
            {mode === "add" ? "Add Branch" : "Edit Branch"}
          </h3>
          <button
            onClick={onClose}
            className="absolute right-5 top-1/2 -translate-y-1/2 text-2xl text-gray-500 hover:text-gray-700"
          >
            √ó
          </button>
        </div>

        {/* Body */}
        <div className="p-6 grid grid-cols-1 md:grid-cols-2 gap-6">
          <Field label="Branch Name" required error={errors.branchName}>
            <Input value={form.branchName} onChange={(e) => update("branchName", e.target.value)} />
          </Field>

          <Field label="Address" required error={errors.address}>
            <Input value={form.address} onChange={(e) => update("address", e.target.value)} />
          </Field>

          <Field label="Phone">
            <Input value={form.phone} onChange={(e) => update("phone", e.target.value)} />
          </Field>


<Field label="Company" required error={errors.companyId}>
  <select
    className="w-full rounded-md border px-3 py-2 dark:bg-gray-800"
    value={form.companyId}
    onChange={(e) => update("companyId", e.target.value)}
    disabled={loadingCompanies}
  >
    <option value="">
      {loadingCompanies ? "Loading companies..." : "Select company"}
    </option>



    {companies.map((company) => (
      <option key={company.id} value={company.id}>
        {company.name}
      </option>
    ))}
  </select>
</Field>

        </div>

        {/* Footer */}
        <div className="p-5 border-t dark:border-gray-800 flex justify-end gap-3">
          <Button variant="outline" onClick={onClose}>Cancel</Button>
          <Button onClick={submit} disabled={loading}>
            {loading ? "Saving..." : mode === "add" ? "Create" : "Update"}
          </Button>
        </div>
      </div>
    </div>
  );
}

/* ---------- Small Helper ---------- */
function Field({
  label,
  children,
  error,
  required,
  full,
}: {
  label: string;
  children: React.ReactNode;
  error?: string;
  required?: boolean;
  full?: boolean;
}) {
  return (
    <div className={full ? "md:col-span-2" : ""}>
      <Label>
        {label} {required && <span className="text-red-500">*</span>}
      </Label>
      {children}
      {error && <p className="text-sm text-red-600 mt-1">{error}</p>}
    </div>
  );
}
